import React from "react";
import Context from "../../Context";
import {injectIntl} from "react-intl";
import key from "../../util/key";
import {
  PlusOutlined,
  QuestionCircleOutlined,
  SearchOutlined,
  UnorderedListOutlined,
} from "@ant-design/icons";
import DataTable from "../../components/Datatable/DataTable";
import DataTableSearch from "../../components/Datatable/DataTableSearch";
import Statelabel from "../../util/Statelabel";
import {Button, Modal} from "antd";
import DataTablePeriod from "../../components/Datatable/DataTablePeriod";
import {Helmet} from "react-helmet";
import Flex from "../../util/Flex";
import {Link} from "react-router-dom";

//import InvoiceTransactions from "./InvoiceTransactions";

class Accounts extends React.Component {
  static contextType = Context;

  constructor(props) {
    super(props);
    this.state = {
      tableUuid: key(),
      columns: [],

      showTransactions: false,
      currentItem: {},
    };
  }

  m = (key, prefix = "account.") => {
    const {intl} = this.props;
    return intl.formatMessage({id: prefix + key});
  };

  componentDidMount() {
    this.setColumns();
  }

  componentDidUpdate(prevProps) {
    if (prevProps.intl.locale !== this.props.intl.locale) {
      this.setColumns();
    }
  }

  onMethodFinish = () => {
    this.setState({
      tableUuid: key(),
    });
    if (this.props.onMethodFinish) {
      this.props.onMethodFinish();
    }
  };

  showTransactions = (currentItem) => {
    this.setState({currentItem, showTransactions: true});
  };

  closeTransactions = () => {
    this.setState({showTransactions: false});
  };

  setColumns = () => {
    this.setState({
      columns: [

        {
          text: true,
          title: this.m("code"),
          dataIndex: "code",
          sorter: true,
          width: "25%",
          render: (value, item) => (
            <>
              <div>{value}</div>
              <div>{item.label}</div>
              {item.description && (
                <div className="gx-font-weight-light gx-text-small gx-text-muted">
                  {item.description}
                </div>
              )}
            </>
          ),

          filterDropdown: (props) => <DataTableSearch {...props} />,
          filterIcon: (filtered) => (
            <SearchOutlined
              style={{color: filtered ? "#1890ff" : undefined}}
            />
          ),
        },

        {
          text: true,
          title: this.m("client"),
          dataIndex: "clientname",
          sorter: true,
          width: "25%",
          render: (value, item) => <>
            <Link to={`/crm/clients/${item.client}`}>{value}</Link>

            {item.clientcode && (
              <div className="gx-font-weight-light gx-text-small gx-text-muted">
                {item.clientcode}
              </div>
            )} </>,

          filterDropdown: (props) => <DataTableSearch {...props} />,
          filterIcon: (filtered) => (
            <SearchOutlined
              style={{color: filtered ? "#1890ff" : undefined}}
            />
          ),
        },

        {
          text: true,
          title: this.m("currency"),
          dataIndex: "currencycode",
          sorter: true,
          width: "10%",
          render: (value, item) => <>
            <div>{item.currencycode} ({item.currencyname})</div>

            {item.currencydescription && (
              <div className="gx-font-weight-light gx-text-small gx-text-muted">
                {item.currencydescription}
              </div>
            )} </>,
          filterDropdown: (props) => <DataTableSearch {...props} />,
          filterIcon: (filtered) => (
            <SearchOutlined
              style={{color: filtered ? "#1890ff" : undefined}}
            />
          ),
        },

        {
          text: true,
          width: "5%",
          title: this.m("balance"),
          dataIndex: "balance",
          sorter: true,
          render: (value, item) => <>{value ? `${value}` : "0"}</>,


        },
        {
          width: "5%",
          title: this.m("created"),
          dataIndex: "created",
          sortDirections: "descend",
          defaultSortOrder: true,
          sorter: true,
          render: (value) => {
            if (value) {
              const dt = new Date(value);
              return (
                <>
                  {dt.toLocaleDateString()}
                  <br/>
                  {dt.toLocaleTimeString()}
                </>
              );
            }
          },

          date: true,
          filterDropdown: (props) => <DataTablePeriod {...props} />,
        },
        {
          width: "5%",
          title: this.m("statename"),
          dataIndex: "state",
          sorter: true,
          render: (value, item) => (
            <div className="gx-text-right">
              <Statelabel onFinish={this.onMethodFinish} {...item} />
            </div>
          ),
        },
      ],
    });
  };

  render() {
    return (
      <div>
        <Helmet>
          <title>{this.m("accounts")}</title>
        </Helmet>
        <h2 className="title gx-mb-4">{this.m("accounts")}</h2>
        <Modal
          visible={this.state.showTransactions}
          footer={null}
          onCancel={this.closeTransactions}
        >
        </Modal>
        <DataTable
          uuid={this.state.tableUuid}
          columns={this.state.columns}
          path={"/account/list"}
        />
      </div>
    );
  }
}

export default injectIntl(Accounts);
